@import '../../utils/colors/colors.scss';

.container {
    display: flex;
    flex: 1;
    background-color: $main;
    height: 60px;
    flex-direction: row;
    justify-content: space-between;
    padding: 0 20px 0 20px;
    align-items: center;
}

.logoWrapper {
    display: flex;
    border-radius: 10px;
}

.logo {
    width: 50px;
    height: 50px;
    border-radius: 100px;
}

.navigations {
    display: flex;
    flex-direction: row;
}

h4 {
    padding: 0 10px 0 0;
    font-family: 'Lego';
}
